.profile {
    background-color: #ffffff;
    padding: 30px 0 0 0;
    width: 400px;
    margin: auto;
}

.description {
    margin: 0 auto;
    padding: 50px 10px;
    text-align: center;
    width: 300px;
    color: grey;
    font-size: 18px;
}

.avatar {
    border-radius: 50%;
}

.name {
    color: black;
    font-weight: bold;
}

.tag .location {
    font-size: 16px;
    color: grey;
}

.stats {
    display: flex;
    justify-content: space-around;
    list-style: none;
    background-color: #f3f6f9;
    padding: 0 0 0 0;
    text-align: center;

}

.stats>li {
    border: 1px solid rgba(128, 128, 128, 0.212);
    text-align: center;
    width: 33.33333%;
    padding: 0 0 20px 0;
}

.label {
    display: flex;
    flex-direction: column;
    padding: 20px 0;
    font-size: 11px;
    text-transform: uppercase;
}